'use client';

import Link from 'next/link';
import { useState } from 'react';
import {
    LayoutDashboard,
    ArrowLeftFromLine,
    ArrowRightFromLine,
    ReceiptText,
    Users,
    Wallet,
    Settings,
} from 'lucide-react';

const navItems = [
    { label: 'Dashboard', href: '/', icon: LayoutDashboard },
    { label: 'Transactions', href: '/transactions', icon: ReceiptText },
    { label: 'Customers', href: '/customers', icon: Users },
    { label: 'Payouts', href: '/payouts', icon: Wallet },
    { label: 'Settings', href: '/settings', icon: Settings },
];

export default function Sidebar() {
    const [isOpen, setIsOpen] = useState(true);

    return (
        <aside
            className={`sticky top-0 flex h-screen flex-col border-r border-slate-200 bg-white/90 backdrop-blur transition-all ${
                isOpen ? 'w-64' : 'w-20'
            }`}
        >
            <div className="flex items-center justify-between px-4 py-5">
                <div className="flex items-center gap-3">
                    <div className="flex h-9 w-9 items-center justify-center rounded-xl bg-slate-900 text-sm font-semibold text-white">
                        P
                    </div>
                    <div className={`${isOpen ? 'block' : 'hidden'}`}>
                        <p className="text-sm font-semibold text-slate-900">Payflow</p>
                        <p className="text-xs text-slate-500">Admin Console</p>
                    </div>
                </div>
                <button
                    type="button"
                    onClick={() => setIsOpen((prev) => !prev)}
                    className="rounded-lg p-1 text-slate-500 transition hover:bg-slate-100 hover:text-slate-900"
                    aria-label={isOpen ? 'Collapse sidebar' : 'Expand sidebar'}
                >
                    {isOpen ? <ArrowLeftFromLine size={18} /> : <ArrowRightFromLine size={18} />}
                </button>
            </div>

            <nav className="flex-1 space-y-1 px-3">
                {navItems.map((item) => (
                    <Link
                        key={item.label}
                        href={item.href}
                        className="group flex items-center gap-3 rounded-xl px-3 py-2 text-sm font-medium text-slate-600 transition hover:bg-slate-100 hover:text-slate-900"
                        title={item.label}
                    >
                        <item.icon className="h-4 w-4 text-slate-500 group-hover:text-slate-900" />
                        <span className={`${isOpen ? 'block' : 'hidden'}`}>{item.label}</span>
                    </Link>
                ))}
            </nav>

            <div className="px-4 pb-6">
                <div className="rounded-2xl border border-slate-200 bg-slate-50 px-3 py-4 text-xs text-slate-500">
                    {isOpen ? (
                        <p>
                            Scheduled payout: <span className="font-semibold text-slate-700">$12,430</span>
                        </p>
                    ) : (
                        <p className="text-center font-semibold text-slate-600">$12k</p>
                    )}
                </div>
            </div>
        </aside>
    );
}